<template>
  <div class="container-fluid single-choice-2">
    <div class="row">

      <div class="col-5 col-promo fixed-top">
      </div> <!-- /col -->

      <div class="col-5 offset-6">

        <div class="row row-question">
          <div class="col">
            <h2>
              {{questionTitle}}
            </h2>
          </div>
        </div>

        <div class="row row-options">
          <div class="col">
            <button class="btn btn-block btn-outline-dark btn-lg text-left"
              v-for="option in options"
              :key="option.id"
              :class="{ 'btn-success': option.isRigth, 'btn-danger':  option.isWrong }"
              @click="$store.dispatch('singleChoice2/submitAnswer', option)">
              {{option.title}}
            </button>
          </div> <!-- /col -->
        </div> <!-- /row -->

      </div> <!-- /col -->

      <div class="col-1"></div>

    </div> <!-- /row -->
  </div> <!-- /container -->
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'SingleChoice2',

  mounted() {
    // Kick things off by asking our chapter module for the next question.
    this.$store.dispatch('chapter/nextQuestion');
  },

  computed: {
    ...mapState('singleChoice2', {
      questionTitle: state => state.questionTitle,
      options: state => state.options,
    }),
  },
};
</script>

