<template>
  <div class="columns">
    <div class="column is-5 fixed-column" v-if="!timelineShowAnswer">
      <TimelineQuestion :question="question"
        :noQuestions="noQuestions"
        :noAnsweredQuestions="noAnsweredQuestions"
        :flags="flags" />
    </div>
    <div class="column is-6 is-offset-6" v-if="!timelineShowAnswer">
      <div class="timeline">
        <div class="timeline-item" v-for="post in timeline" :key="post.id"
          @mouseover="post.isHovered = true"
          @mouseout="post.isHovered = false"
          @click="guess(post)"
          :class="[
            {'is-info': post.isHovered},
            {'hover-timeline': post.isHovered && post.isFiller},
            {
              'is-info': post.state === 'default',
              'is-danger': post.state === 'wrong',
              'is-success': post.state === 'correct',
            }
          ]">
          <div class="timeline-marker is-timeline-year has-text-centered"
            v-if="post.startYear === post.endYear && !post.isFiller"
            :class="{
              'is-info': post.state === 'default',
              'is-danger': post.state === 'wrong',
              'is-success': post.state === 'correct',
            }">
            <span class="has-text-white has-text-weight-medium" :class="{
              'has-text-weight-bold': post.isHovered,
            }">
              {{post.startYear}}
            </span>
          </div>
          <div class="timeline-content">
            <p class="heading" :class="{ 'has-text-weight-bold': post.isHovered }">
              <span v-if="post.startYear === post.endYear && post.isFiller">
                {{post.startYear}}
              </span>
              <span v-else-if="post.startYear && post.endYear && post.isFiller">
                {{post.startYear}} - {{post.endYear}}
              </span>
              <span v-else-if="post.startYear && !post.endYear && post.isFiller">
                {{post.startYear}} -
              </span>
              <span v-else-if="!post.startYear && post.endYear && post.isFiller">
                - {{post.endYear}}
              </span>
            </p>
            <p v-html="post.title"></p>
          </div>
        </div>
      </div>
    </div>
    <div class="column is-12" v-if="timelineShowAnswer">
      <TimelineAnswer :question="question" :noRemainingQuestions="noRemainingQuestions" :flags="flags" />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import TimelineAnswer from '@/components/TimelineAnswer.vue';
import TimelineQuestion from '@/components/TimelineQuestion.vue';

export default {
  name: 'Timeline',
  components: {
    TimelineAnswer,
    TimelineQuestion,
  },
  props: ['flags'],
  data() {
    return {
      questions: [],
      question: {},
      timeline: [],
      noQuestions: 0,
      noAnsweredQuestions: 0,
    };
  },
  computed: {
    ...mapState('questions', {
      posts: state => state.questions,
    }),
    ...mapState([
      'timelineShowAnswer',
    ]),
    noRemainingQuestions() {
      return this.noQuestions - this.noAnsweredQuestions;
    },
  },
  mounted() {
    this.questions = this.posts.map(post => ({
      ...post,
      startYear: post.year,
      endYear: post.year,
    }));

    const post = this.useQuestion();
    this.insertPostOnTimeline(post);
    this.noQuestions = this.questions.length;
    this.question = this.useQuestion(this.questions);
  },
  methods: {
    guess(post) {
      const guessStartYear = post.startYear ? post.startYear : this.question.startYear;
      const guessEndYear = post.endYear ? post.endYear : this.question.endYear;

      if (guessStartYear <= this.question.startYear && guessEndYear >= this.question.endYear) {
        post.state = 'correct';
        this.correctGuess();
      } else {
        post.state = 'wrong';
      }
    },
    correctGuess() {
      this.timeline = this.timeline.map(post => ({
        ...post,
        state: 'default',
      }));
      this.insertPostOnTimeline(this.question);
      this.question = this.useQuestion();
      this.$store.commit('TIMELINE_SHOW_ANSWER', true);
      this.noAnsweredQuestions++;
    },
    useQuestion() {
      let question = this.questions.splice(Math.floor(Math.random() * this.questions.length), 1)[0];
      if (typeof question === 'undefined') {
        question = {
          id: this.getRandomId(),
          title: 'Grattis, du klarade det!',
        };
      }
      return question;
    },
    insertPostOnTimeline(post) {
      // Check if a post from the same year already exist on the timeline
      const result = this.timeline.find(item => item.startYear === post.startYear && item.isFiller === false);

      if (typeof result === 'undefined') {
        this.timeline.push({
          ...post,
          isFiller: false,
          isHovered: false,
          state: 'default',
        });
      } else {
        result.title = `${result.title}<br/>${post.title}`;
      }

      this.timeline = this.timeline.filter(item => item.isFiller === false);
      this.timeline.sort(this.sortTimeline);
      this.fillers = [];

      // Add fillers.
      let previousYear;
      for (let i = 0; i < this.timeline.length; i++) {
        if (i === 0 || (this.timeline[i].startYear - previousYear) > 1) {
          this.fillers.push({
            id: this.getRandomId(),
            startYear: previousYear ? previousYear + 1 : false,
            endYear: this.timeline[i].startYear - 1,
            title: '',
            isFiller: true,
            isHovered: false,
            state: 'default',
          });
        }
        previousYear = this.timeline[i].startYear;
      }

      this.fillers.push({
        id: this.getRandomId(),
        startYear: previousYear + 1,
        endYear: false,
        title: '',
        isFiller: true,
        isHovered: false,
        state: 'default',
      });

      this.timeline = this.timeline.concat(this.fillers);
      this.timeline.sort(this.sortTimeline);
    },
    sortTimeline(a, b) {
      if (a.startYear < b.startYear) {
        return -1;
      }
      if (a.startYear > b.startYear) {
        return 1;
      }
      return 0;
    },
    getRandomId() {
      return Math.floor(Math.random() * (9999999 - 100000) + 100000);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.fixed-column {
  position: fixed;
}
.timeline-item {
  cursor: pointer;
}
.timeline .timeline-item.hover-timeline::before {
  width: 5px;
  left: -2.5px;
}
.timeline .timeline-item .timeline-marker.is-timeline-year {
  border-radius: 10%;
  width: auto;
  height: auto;
  padding-left: 5px;
  padding-right: 5px;
}
</style>
