<template>
  <div class="container-fluid answer text-center">
    <div class="answer-animation">
      <transition appear name="bounce">
        <font-awesome-icon v-if="isCorrect === true" :icon="['far', 'check-circle']" class="text-success" size="4x" />
        <font-awesome-icon v-else :icon="['far', 'times-circle']" class="text-danger" size="4x" />
      </transition>
    </div> <!-- /answer-animation -->
    <div v-if="isCorrect === false">
      <p>Rätt svar: {{correctAnswer}}</p>
    </div>
    <p>{{description}}</p>
    <img v-if="image" :src="image" class="img-fluid rounded answer-image">
    <button type="button" class="btn btn-primary btn-lg btn-block" @click="nextQuestion">Fortsätt</button>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'Answer',

  computed: {
    ...mapState('answer', {
      isCorrect: state => state.isCorrect,
      correctAnswer: state => state.correctAnswer,
      description: state => state.description,
      image: state => state.image,
    }),
  },

  methods: {
    nextQuestion() {
      this.$store.commit('answer/setIsActive', false);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .answer-animation {
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .answer-image {
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .bounce-enter-active {
    animation: bounce-in 1.0s;
  }

  .bounce-leave-active {
    animation: bounce-in 1.0s reverse;
  }

  @keyframes bounce-in {
    0% {
      transform: scale(0);
    }
    50% {
      transform: scale(1.5);
    }
    100% {
      transform: scale(1);
    }
  }
</style>
