<template>
  <div class="columns is-vcentered is-centered">
    <div class="column is-5">
      <div class="columns is-gapless">
        <div class="column is-1">
          <a class="button is-primary is-large img-nav" @click="$store.dispatch('questions/getPreviousQuestion')">
            <font-awesome-icon icon="chevron-left" />
          </a>
        </div>
        <div class="column is-10">
          <figure class="image is-3by2">
            <img :src="question.img">
          </figure>
        </div>
        <div class="column is-1">
          <a class="button is-primary is-large img-nav" @click="$store.dispatch('questions/getNextQuestion')">
            <font-awesome-icon icon="chevron-right" />
          </a>
        </div>
      </div>
    </div>
    <div class="column is-2 guess-column">
      <button class="button is-primary is-large guess-button"
        @click="$store.dispatch('questions/submitMatchImageAnswer')">
        <font-awesome-icon icon="check-circle" />
      </button>
    </div>
    <div class="column is-5">
      <div class="columns is-gapless">
        <div class="column is-1">
          <a class="button is-primary is-large img-nav" @click="$store.dispatch('questions/getPreviousOption')">
            <font-awesome-icon icon="chevron-left" />
          </a>
        </div>
        <div class="column is-10">
          <figure class="image is-3by2">
            <img :src="option.img">
          </figure>
        </div>
        <div class="column is-1">
          <a class="button is-primary is-large img-nav" @click="$store.dispatch('questions/getNextOption')">
            <font-awesome-icon icon="chevron-right" />
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'MatchImage2',
  mounted() {
    this.$store.dispatch('questions/shuffleQuestions');
    this.$store.dispatch('questions/shuffleOptions');
    this.$store.dispatch('questions/getNextQuestion');
    this.$store.dispatch('questions/getNextOption');
  },
  computed: {
    ...mapState('questions', {
      question: state => state.currentQuestion,
      option: state => state.currentOption,
    }),
  },
};
</script>

