<template>
  <div>
    <div class="chapter" v-if="! isAnswerActive && ! isQuizCompleted">
      <MatchImage v-if="chapterType === 'matchImage'" />
      <SingleChoice2 v-if="chapterType === 'singleChoice2'" />
      <PairImages v-if="chapterType === 'pairImages'" />
      <Timeline v-if="chapterType === 'timeline'" />
    </div>
    <Answer v-if="isAnswerActive" />
    <Completed v-if="isQuizCompleted" />
  </div>
</template>

<script>
import { mapState } from 'vuex';
import Answer from '@/components/Answer.vue';
import Completed from '@/components/Completed.vue';
import MatchImage from '@/components/MatchImage2.vue';
import SingleChoice2 from '@/components/SingleChoice2.vue';
import PairImages from '@/components/PairImages.vue';
import Timeline from '@/components/Timeline.vue';

export default {
  name: 'Quiz',
  components: {
    Answer,
    Completed,
    MatchImage,
    PairImages,
    SingleChoice2,
    Timeline,
  },

  data() {
    return {
      flags: {
        hasImageBackground: true,
      },
    };
  },

  mounted() {
    // For debug purposes we provide a shortcut to start quiz at a specific
    // chapter.
    const chapterId = typeof this.$route.params.chapterId !== 'undefined' ? this.$route.params.chapterId : null;

    // Start the requested quiz. If a chapter ID was provided the quiz will
    // start at the requested chapter.
    this.$store.dispatch('quiz/startQuiz', {
      quizId: this.$route.params.quizId,
      chapterId,
    });
  },

  computed: {
    ...mapState('answer', {
      isAnswerActive: state => state.isActive,
    }),
    ...mapState('chapter', {
      chapterType: state => state.chapterType,
    }),
    ...mapState('quiz', {
      isQuizCompleted: state => state.isCompleted,
    }),
  },
};
</script>
