<template>
  <section class="hero is-success is-fullheight">
    <div class="hero-body">
      <div class="container has-text-centered">
        <h1 class="title is-2 is-spaced is-question">
          Rätt!
        </h1>
        <h2 class="subtitle is-5 is-question-subtitle">
        </h2>
        <button v-if="noRemainingQuestions !== 0" class="button is-success is-light" @click="closeAnswer()">
          Nästa fråga
        </button>
        <button v-else class="button is-success is-light" @click="closeAnswer()">
          Fortsätt
        </button>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'TimelineAnswer',
  props: ['question', 'noRemainingQuestions', 'flags'],
  methods: {
    closeAnswer() {
      this.$store.commit('TIMELINE_SHOW_ANSWER', false);
      if (this.noRemainingQuestions === 0) {
        this.$store.dispatch('branches/getNextBranch');
      }
    },
  },
};
</script>

