<template>
  <div class="container-fluid single-choice-2">
    <div class="row">

      <div class="col-md-5 col-promo fixed-top">
      </div> <!-- /col -->

      <div class="col col-md-5 offset-md-6 col-question">

        <div class="row">
          <div class="col">
            <img :src="questionImage" class="img-fluid rounded">
          </div>
        </div>
        <div class="row">
          <div class="col col-question-title text-center">
            <h3>Vilken modell är detta?</h3>
            <h6 class="text-muted">Välj rätt modell bland bilderna nedanför.</h6>
          </div>
        </div>

        <div class="row row-options" v-for="(set, index) in sets" :key="index">
          <div class="col col-option" v-for="option in set" :key="option.id">
            <div class="option-overlay rounded"
              :class="{ 'bg-success': option.isRight, 'bg-danger': option.isWrong }"
              @click="submitAnswer(option)">
              <img v-if="option.img"
                :src="option.img"
                class="img-fluid img-option">
            </div>
          </div> <!-- /col -->
        </div> <!-- /row -->

      </div> <!-- /col -->

      <div class="col-md-1"></div>

    </div> <!-- /row -->
  </div> <!-- /container -->
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'SingleChoice2',

  mounted() {
    // Kick things off by asking our chapter module for the next question.
    this.$store.dispatch('chapter/nextQuestion', { skipLastQuestion: true });
  },

  computed: {
    ...mapState('pairImages', {
      questionImage: state => state.questionImage,
      options: state => state.options,
    }),

    sets() {
      const columns = 2;
      const sets = [];
      let set = [];
      for (let i = 0; i < this.options.length; i++) {
        set.push(this.options[i]);
        if (i % columns === columns - 1) {
          sets.push(set);
          set = [];
        }
      }
      if (set.length !== 0) {
        const pad = columns - set.length;
        for (let i = 0; i < pad; i++) {
          set.push({});
        }
        sets.push(set);
      }
      return sets;
    },
  },

  methods: {
    submitAnswer(option) {
      if (option.isRight === null && option.isWrong === null) {
        this.$store.dispatch('pairImages/submitAnswer', option);
      }
    },
  },
};
</script>

